<template><markdown-wrapper><h1 id="datepicker">DatePicker <a class="header-anchor" href="#datepicker" aria-hidden="true">&#128279;</a></h1>
<blockquote>
<p>A lightweight &amp; configurable date picker.</p>
</blockquote>
<h2 id="example">Example <a class="header-anchor" href="#example" aria-hidden="true">&#128279;</a></h2>
<p>Use <code v-pre>v-model</code> to bind the selected date.</p>
<date-picker-example ref="date-picker-example"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">section</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">date-picker</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"date"</span>/&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span> <span class="hljs-attr">v-show</span>=<span class="hljs-string">"date"</span>&gt;</span>You selected <span class="hljs-tag">&lt;<span class="hljs-name">b</span>&gt;</span>{{date}}<span class="hljs-tag">&lt;/<span class="hljs-name">b</span>&gt;</span>.<span class="hljs-tag">&lt;/<span class="hljs-name">alert</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">section</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">date</span>: <span class="hljs-literal">null</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- date-picker-example.vue --&gt;</span>
</code></pre>
<h2 id="formats">Formats <a class="header-anchor" href="#formats" aria-hidden="true">&#128279;</a></h2>
<p>You can use any format you like. For example:</p>
<ul>
<li>yyyy-M-d</li>
<li>yyyy-MM-dd</li>
<li>yyyy-MMM-dd</li>
<li>yyyy-MMMM-dd</li>
<li>yyyy/MM/dd</li>
<li>MM/dd/yyyy</li>
<li>yyyy, MM, dd</li>
<li>...</li>
</ul>
<p>Note that some browsers (e.g. IE) might not support all of these formats.</p>
<p>If you need a special format that not supported by <code v-pre>Date.parse</code>, consider using <code v-pre>date-parser</code> option to override it.</p>
<date-picker-formats ref="date-picker-formats"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">section</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">date-picker</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"date"</span> <span class="hljs-attr">format</span>=<span class="hljs-string">"yyyy/MMMM/dd"</span>/&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span> <span class="hljs-attr">v-show</span>=<span class="hljs-string">"date"</span>&gt;</span>You selected <span class="hljs-tag">&lt;<span class="hljs-name">b</span>&gt;</span>{{date}}<span class="hljs-tag">&lt;/<span class="hljs-name">b</span>&gt;</span>.<span class="hljs-tag">&lt;/<span class="hljs-name">alert</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">section</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">date</span>: <span class="hljs-literal">null</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- date-picker-formats.vue --&gt;</span>
</code></pre>
<h2 id="buttons">Buttons <a class="header-anchor" href="#buttons" aria-hidden="true">&#128279;</a></h2>
<p>Use <code v-pre>today-btn</code> and <code v-pre>clear-btn</code> to toggle visible of them.</p>
<date-picker-without-buttons ref="date-picker-without-buttons"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">date-picker</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"date"</span> <span class="hljs-attr">:today-btn</span>=<span class="hljs-string">"false"</span> <span class="hljs-attr">:clear-btn</span>=<span class="hljs-string">"false"</span>/&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">date</span>: <span class="hljs-literal">null</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- date-picker-without-buttons.vue --&gt;</span>
</code></pre>
<h2 id="range-limit">Range limit <a class="header-anchor" href="#range-limit" aria-hidden="true">&#128279;</a></h2>
<p>Example that limit date range from <strong>2018-01-01</strong> to <strong>2019-01-01</strong>:</p>
<date-picker-range-limit ref="date-picker-range-limit"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">date-picker</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"date"</span> <span class="hljs-attr">limit-from</span>=<span class="hljs-string">"2018-01-01"</span> <span class="hljs-attr">limit-to</span>=<span class="hljs-string">"2019-01-01"</span>/&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">date</span>: <span class="hljs-literal">null</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- date-picker-range-limit.vue --&gt;</span>
</code></pre>
<h2 id="week-starts">Week starts <a class="header-anchor" href="#week-starts" aria-hidden="true">&#128279;</a></h2>
<p>Change the starting day of the week. Support 0 (Sunday) ~ 6 (Saturday).</p>
<date-picker-week-starts ref="date-picker-week-starts"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">date-picker</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"date"</span> <span class="hljs-attr">:week-starts-with</span>=<span class="hljs-string">"1"</span>/&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">date</span>: <span class="hljs-literal">null</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- date-picker-week-starts.vue --&gt;</span>
</code></pre>
<h2 id="with-dropdown">With dropdown <a class="header-anchor" href="#with-dropdown" aria-hidden="true">&#128279;</a></h2>
<date-picker-dropdown-example ref="date-picker-dropdown-example"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">form</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"form-inline"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">dropdown</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"form-group"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"input-group"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">input</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"form-control"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"date"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"input-group-btn"</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"dropdown-toggle"</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"glyphicon glyphicon-calendar"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"dropdown"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">li</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">date-picker</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"date"</span>/&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">li</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">dropdown</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">form</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">date</span>: <span class="hljs-literal">null</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- date-picker-dropdown-example.vue --&gt;</span>
</code></pre>
<h2 id="custom-icons">Custom icons <a class="header-anchor" href="#custom-icons" aria-hidden="true">&#128279;</a></h2>
<date-picker-icons-example ref="date-picker-icons-example"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">section</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">date-picker</span> <span class="hljs-attr">icon-control-left</span>=<span class="hljs-string">"glyphicon glyphicon-triangle-left"</span> <span class="hljs-attr">icon-control-right</span>=<span class="hljs-string">"glyphicon glyphicon-triangle-right"</span>/&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">section</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-comment">&lt;!-- date-picker-icons-example.vue --&gt;</span>
</code></pre>
<h1 id="api-reference">API Reference <a class="header-anchor" href="#api-reference" aria-hidden="true">&#128279;</a></h1>
<h2 id="datepicker-2"><a href="https://github.com/wxsms/uiv/blob/master/src/components/datepicker/DatePicker.vue">DatePicker</a> <a class="header-anchor" href="#datepicker-2" aria-hidden="true">&#128279;</a></h2>
<h3 id="props">Props <a class="header-anchor" href="#props" aria-hidden="true">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Type</th>
<th>Default</th>
<th>Required</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>v-model</code></td>
<td></td>
<td></td>
<td>âœ”</td>
<td>The selected date.</td>
</tr>
<tr>
<td><code v-pre>width</code></td>
<td>Number</td>
<td>270</td>
<td></td>
<td>The date-picker's width in px.</td>
</tr>
<tr>
<td><code v-pre>today-btn</code></td>
<td>Boolean</td>
<td>true</td>
<td></td>
<td>Show / hide the today button.</td>
</tr>
<tr>
<td><code v-pre>clear-btn</code></td>
<td>Boolean</td>
<td>true</td>
<td></td>
<td>Show / hide the clear button.</td>
</tr>
<tr>
<td><code v-pre>format</code></td>
<td>String</td>
<td>yyyy-MM-dd</td>
<td></td>
<td>The date format.</td>
</tr>
<tr>
<td><code v-pre>close-on-selected</code></td>
<td>Boolean</td>
<td>true</td>
<td></td>
<td>Close the date-picker dropdown after date selected.</td>
</tr>
<tr>
<td><code v-pre>limit-from</code></td>
<td></td>
<td></td>
<td></td>
<td>Anything that can convert to a valid Date object. E.g. <code v-pre>2017-01-01</code> or <code v-pre>new Date()</code>.</td>
</tr>
<tr>
<td><code v-pre>limit-to</code></td>
<td></td>
<td></td>
<td></td>
<td>Same as <code v-pre>limit-from</code>.</td>
</tr>
<tr>
<td><code v-pre>initial-view</code></td>
<td>String</td>
<td>d</td>
<td></td>
<td>Open the date-picker with specify view (one of <code v-pre>d</code> / <code v-pre>m</code> / <code v-pre>y</code>) on initial. Only works if the <code v-pre>v-model</code> is empty.</td>
</tr>
<tr>
<td><code v-pre>week-starts-with</code></td>
<td>Number</td>
<td>0</td>
<td></td>
<td>Starting day of the week. Support 0 (Sunday) ~ 6 (Saturday).</td>
</tr>
<tr>
<td><code v-pre>date-parser</code></td>
<td>Function</td>
<td>Date.parse</td>
<td></td>
<td>Use this prop to replace the <code v-pre>Date.parse</code> call inside the component. Useful when The formatted String can not be correctly parsed to Date type by <code v-pre>Date.parse</code> (e.g. dd-MM-yyyy).</td>
</tr>
<tr>
<td><code v-pre>icon-control-left</code></td>
<td>String</td>
<td>glyphicon glyphicon-chevron-left</td>
<td></td>
<td>The arrow icon shown inside the <code v-pre>previous</code> button.</td>
</tr>
<tr>
<td><code v-pre>icon-control-right</code></td>
<td>String</td>
<td>glyphicon glyphicon-chevron-right</td>
<td></td>
<td>The arrow icon shown inside the <code v-pre>next</code> button.</td>
</tr>
</tbody>
</table></div></markdown-wrapper></template><script>
  

  

  

  

  

  
export default {components:{'date-picker-example':{"template":"<div class=\"markdown-live-example\">\n  <section>\n    <date-picker v-model=\"date\"/>\n    <br/>\n    <alert type=\"info\" v-show=\"date\">You selected <b>{{date}}</b>.</alert>\n  </section>\n</div>",
    data () {
      return {
        date: null
      }
    }
  },'date-picker-formats':{"template":"<div class=\"markdown-live-example\">\n  <section>\n    <date-picker v-model=\"date\" format=\"yyyy/MMMM/dd\"/>\n    <br/>\n    <alert type=\"info\" v-show=\"date\">You selected <b>{{date}}</b>.</alert>\n  </section>\n</div>",
    data () {
      return {
        date: null
      }
    }
  },'date-picker-without-buttons':{"template":"<div class=\"markdown-live-example\">\n  <date-picker v-model=\"date\" :today-btn=\"false\" :clear-btn=\"false\"/>\n</div>",
    data () {
      return {
        date: null
      }
    }
  },'date-picker-range-limit':{"template":"<div class=\"markdown-live-example\">\n  <date-picker v-model=\"date\" limit-from=\"2018-01-01\" limit-to=\"2019-01-01\"/>\n</div>",
    data () {
      return {
        date: null
      }
    }
  },'date-picker-week-starts':{"template":"<div class=\"markdown-live-example\">\n  <date-picker v-model=\"date\" :week-starts-with=\"1\"/>\n</div>",
    data () {
      return {
        date: null
      }
    }
  },'date-picker-dropdown-example':{"template":"<div class=\"markdown-live-example\">\n  <form class=\"form-inline\">\n    <dropdown class=\"form-group\">\n      <div class=\"input-group\">\n        <input class=\"form-control\" type=\"text\" v-model=\"date\">\n        <div class=\"input-group-btn\">\n          <btn class=\"dropdown-toggle\"><i class=\"glyphicon glyphicon-calendar\"></i></btn>\n        </div>\n      </div>\n      <template slot=\"dropdown\">\n        <li>\n          <date-picker v-model=\"date\"/>\n        </li>\n      </template>\n    </dropdown>\n  </form>\n</div>",
    data () {
      return {
        date: null
      }
    }
  },'date-picker-icons-example':{"template":"<div class=\"markdown-live-example\">\n  <section>\n    <date-picker icon-control-left=\"glyphicon glyphicon-triangle-left\" icon-control-right=\"glyphicon glyphicon-triangle-right\"/>\n  </section>\n</div>"}}}</script>


